@use 'helpers' as *;

body {
 background-color: rgb(var(--color-green-white));
 font-family: 'Co Headline Light', sans-serif;
 line-height: 1.5;
}

#main-header {
 --max-width: 1366px;

 align-items: start;
 display: flex;
 flex-wrap: wrap;
 gap: 1rem;
 justify-content: space-between;
 padding: fluid(1rem, 2rem);
 padding-block-end: fluid(0.5rem, 1rem);
}

#main-header__logo > img {
 width: fluid(160px, 372px);
}

#main-header__info {
 font-size: fluid(14px, 22px);
}

#main-footer {
 --max-width: 800px;

 background-color: rgb(var(--color-coriander));
 font-size: fluid(14px, 16px);
 padding-inline: fluid(1rem, 4rem);
 position: relative;
}

#main-footer > div {
 align-items: center;
 display: flex;
 flex-wrap: wrap;
 gap: fluid(2rem, 4rem);
 padding-block: fluid(85px, 150px) fluid(2rem, 4rem);

 @media (max-width: 950px) {
 justify-content: center;
 }
}

#main-footer iframe {
 aspect-ratio: 1;
 border: 1px solid black;
 border-radius: 0.5rem;
 max-width: fluid(280px, 400px);
}

#main-footer__copyright {
 font-size: 10px;
 padding-block-end: 6px;
 text-align: center;
}

.hours {
 padding-inline-start: calc(1em + 1ch);
}

#hero {
 --max-width: 1366px;

 background-image: url('/images/hero.jpg'); /* TODO: responsive sizes */
 background-size: cover;
 height: calc(100 * var(--vh, 1vh) - var(--header-height));
 min-height: calc(100vw * 0.55);
 position: relative;
}

#hero > img {
 object-fit: cover;
 object-position: center 25%;
}

main {
 --max-width: 720px;

 background: linear-gradient(to bottom, rgb(var(--color-green-white)), white, rgb(var(--color-green-white)));
 display: flex;
 flex-direction: column;
 font-size: fluid(14px, 20px);
 margin-block: fluid(1.5rem, 4rem) fluid(0.75rem, 2rem);
 padding-inline: fluid(1.5rem, 4rem);
 row-gap: fluid(3rem, 6rem);
 text-align: center;
}

#services {
 column-gap: calc(var(--service-block-ratio) * 2rem);
 display: flex;
 flex-wrap: wrap;
 justify-content: center;
 margin-inline: auto;
 max-width: calc(144% - fluid(64%, 80%));
}

.service-block {
 width: calc((100% - 2 * var(--service-block-ratio) * 2rem) / 3);
}

.service-block:not(:nth-last-child(-n + 2)) {
 margin-block-end: 7%;
}

#services h2 {
 font-size: max(14px, calc(var(--service-block-ratio) * 20px));
 line-height: 1.20;
 margin-inline: -50%;
}

#services img {
 filter: sepia(0.25);
 height: auto;
 margin-bottom: 1rem;
}